import { useState } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { Button, Form, Input, Typography, Row, Col, Layout } from 'antd';
import { SendOutlined } from '@ant-design/icons';
import useMediaQuery from 'use-media-antd-query';


// TODO: React HookForm
// sendgridGAPI

const { TextArea } = Input;
const { Title } = Typography;

interface FormValue {
  name: string;
  email: string;
  phone: number;
  company: string;
  message: string;
}


export const ContactComponent = () => {
  const colSize = useMediaQuery();
  const [data, setData] = useState();
  const {
    control,
    handleSubmit
  } = useForm();

  const postMessage = async (data: any) => {
    fetch('https://new-express-project-navy.vercel.app/sendToSlack', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ data: data.email })
    })
      .then(response => {
        console.log('res', response)
        if (response.ok) {
          console.log('Slack');
        } else {
          console.error('Slack');
        }
      })
      .catch(error => {
        console.error('Slack:', error);
      });
  }

  const onSubmit = (data: any) => {
    console.log(data);
    postMessage(data);
  };

  return (
    <Layout id='contact'>
      <Form
        onFinish={handleSubmit(onSubmit)}
        layout="vertical"
        wrapperCol={{ span: 12 }}
        style={{ padding: '50px 0' }}
      >
        <Row
          justify={{ xs: 'start', lg: 'center', xl: 'center' }}
          align='middle'
        >
          <Col xs={20} lg={18} xl={18} offset={colSize === 'lg' || colSize === 'xl' ? 10 : 2}>
            <Title>Contact</Title>
            {/*  */}
            <Controller
              control={control}
              name='name'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                  help={error?.message}
                  rules={[{ required: true, message: '' }]}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='company'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='email'
              rules={{
                required: true,

              }}
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                  help={error?.message}
                  rules={[{ required: true, message: '' }]}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='phone'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='message'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                  help={error?.message}
                  rules={[{ required: true, message: '' }]}
                >
                  <TextArea
                    {...field}
                  />
                </Form.Item>
              )}
            />

            <Row justify="start">
              <Col xl={20} lg={20} xs={12} offset={colSize === 'lg' || colSize === 'xl' ? 4 : 4}>
                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    shape="round"
                    size='large'
                    icon={<SendOutlined />}
                  >
                    
                  </Button>
                </Form.Item>
              </Col>
            </Row>

          </Col>
        </Row>
      </Form>
    </Layout>
  )
}
